* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

/* ------------------------------- NAVBAR ------------------------------- */

.navbar {
  font-family: "Roboto Slab", serif;
  font-size: 18px;
  background-color: black;
  border: none;
  padding-bottom: 10px;
  width: 100%;
  position: fixed;
  z-index: 5;
}

.main-nav {
  list-style-type: none;
  display: none;
}

.nav-links,
.logo {
  text-decoration: none;
  color: rgba(255, 255, 255, 1);
}

.main-nav li {
  text-align: center;
  margin: 15px auto;
}

.logo {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  font-size: 22px;
  margin-top: 10px;
  margin-left: 20px;
}

.navbar-toggle {
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.8);
  font-size: 24px;
}

.active {
  display: block;
}

@media screen and (min-width: 768px) {
  .navbar {
    display: flex;
    justify-content: space-between;
    padding-bottom: 0;
    height: 50px;
    align-items: center;
  }

  .main-nav {
    display: flex;
    margin-right: 30px;
    flex-direction: row;
    justify-content: flex-end;
  }

  .main-nav li {
    margin: 0;
  }

  .nav-links {
    margin: 0 15px 0 15px;
  }

  .logo {
    margin-top: 0;
  }

  .navbar-toggle {
    display: none;
  }

  .logo:hover,
  .nav-links:hover {
    color: rgba(255, 255, 255, 0.7);
  }
}

.wrapper {
  width: 100%;
  margin: 0 50px 0 0;
  padding: 0;
}

.container {
  width: 100%;
  padding: 50px 0;
}

/* ------------------------------- ANIMATIONS ------------------------------- */

@keyframes fadeIn {
  0% {
    transform: translateY(-10%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes wiggle {
  0% {
    transform: rotate(5deg);
  }
  25% {
    transform: rotate(-5deg);
  }
  50% {
    transform: rotate(10deg);
  }
  75% {
    transform: rotate(-2.5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes bouncy {
  0%{top:0em}
  40%{top:0em}
  43%{top:-0.9em}
  46%{top:0em}
  48%{top:-0.4em}
  50%{top:0em}
  100%{top:0em;}
}

.bouncy {
  animation: bouncy 3.5s infinite linear;
  position: relative;
}

.fall {
  animation: fadeIn 2s;
}

.grow {
  transition: all 0.2s ease-in-out;
}

.grow:hover {
  transform: scale(1.1);
}

/* ------------------------------- BACKGROUND CONTROL ------------------------------- */

#intro {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("./assets/self-min.jpg") no-repeat center center fixed;
  background-size: cover;
  padding: 20% 0 20% 0;
  text-align: center;
  color: white;
  height: auto;
}

#about {
  background: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5% 0 10% 0;
  height: auto;
}

#skills {
  background: #F0F0F0 no-repeat center center fixed;
  background-size: cover;
  height: auto;
}

#projects {
  background: #542437;
  height: auto;
}

#resume {
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
  url("./assets/office.jpg") no-repeat fixed;
  background-size: cover;
  height: 300px;
}

#contact {
  background: #333;
  height: auto;
}

@media screen and (max-width: 768px) {
  #intro {
    background: linear-gradient(
        rgba(26, 139, 204, 0.7),
        rgba(26, 139, 204, 0.2)
      )
      fixed;
    padding: 20% 0 20% 0;
  }
}

/* ------------------------------- INTRO ------------------------------- */

#intro .title {
  font-size: 90px;
  font-family: "Roboto Slab", serif;
}

#intro .desc {
  font-size: 25px;
  margin-top: 20px;
  font-family: "Source Sans Pro", sans-serif;
  font-style: italic;
  font-weight: bold;
}

#intro .name {
  background-color: #001f3f;
  margin: -20px;
  padding: 0 20px 0 20px;
  border-radius: 25px;
}

/* ------------------------------- ABOUT ------------------------------- */

#about .container {
  width: 75%;
  height: 95%;
  text-align: center;
}

#about h1 {
  font-family: "Roboto Slab", sans-serif;
  font-size: 40px;
  margin-bottom: 25px;
}

#about li{
  margin: 10px;
}

#about .image {
  display: block;
  height: 150px;
  width: 150px;
  border-radius: 50%;
  box-shadow: 2px 2px 5px lightgray;
  margin: auto;
  margin-bottom: 25px;
}

#about .desc {
  font-family: "Roboto Slab", sans-serif;
  font-size: 20px;
  display: inline-block;
  width: 75%;
  height: 60%;
  margin: 10px;
  border-top: 2px solid lightgray;
  border-bottom: 2px solid lightgray;
  padding: 20px;
  line-height: 25px;
}

#about .innerDesc {
  width: 100%;
  text-align: left;
  margin: auto;
  margin-bottom: 25px;
  padding: 0 15px 0 15px;
}
#about .em {
  color: rgba(200, 60, 60, 1);
  font-weight: bold;
}

#about .iconBox {
  width: 60%;
  background: #ddd;
  text-align: center;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

#about .icon {
  max-width: 33%;
}

@media screen and (max-width: 1000px) {
  #about .container {
    padding: 5px;
  }
  #about .desc {
    width: 80%;
    padding: 0 0 10px 0;
  }
}

@media screen and (max-width: 768px) {
  #about .container {
    width: 90%;
    padding: 5px;
  }
  #about .desc {
    width: 85%;
    padding: 0 0 10px 0;
  }
}

/* ------------------------------- SKILLS ------------------------------- */

#skills .container {
  margin: auto;
  width: 100%;
  height: 90%;
  text-align: center;
  padding: 20px;
}

#skills h1 {
  font-family: "Roboto Slab", sans-serif;
  font-size: 50px;
  margin: 25px 0 25px 0;
  color: black;
}

#skills .innerContainer {
  margin: auto;
  width: 768px;
  height: 90%;
  border-radius: 25px;
  padding: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
}

#skills .logoContainer {
  flex: 1;
  padding: 20px;
  font-family: "Source Sans Pro", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  color: black;
}

#skills img {
  max-width: 130px;
}

@media screen and (max-width: 768px) {
  #skills .container {
    padding: 5px;
  }
  #skills .innerContainer {
    width: 100%;
    padding: 0;
  }
}

/* ------------------------------- PROJECTS ------------------------------- */

#projects .container {
  margin: -25px auto;
  width: 100%;
}

#projects h1 {
  text-align: center;
  font-family: "Julius Sans One", sans-serif;
  font-size: 50px;
  font-weight: bold;
  color: white;
  margin-bottom: 25px;
}

#projects h2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
}
#projects h4 {
  text-align: left;
  font-style: italic;
  margin-bottom: 10px;
}

#projects p {
  padding: 5px;
}

#projects .date {
  font-size: 15px;
}

#projects .projectsContainer {
  font-family: "Roboto Slab", sans-serif;
  margin: auto;
  width: 95%;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
}

#projects .project {
  height: auto;
  margin: 10px;
  padding: 10px;
  width: 33%;
  background: linear-gradient(
    rgba(200, 200, 200, 0.1),
    rgba(200, 200, 200, 0.1)
  );
  border-radius: 25px;
  box-shadow: 0 0 5px gray;
}

#projects .misc {
  font-family: "Roboto Slab", sans-serif;
  margin: auto;
  color: white;
  display: flex;
  justify-content: space-evenly;
}

#projects ul {
  list-style-type: none;
}

#projects img {
  width: 100%;
  object-fit: cover;
}

#projects a {
  text-align: center;
  color: aquamarine;
  padding: 5px;
  text-decoration: underline;
}

@media screen and (max-width: 905px) {
  #projects .projectsContainer {
    flex-direction: column;
  }

  #projects .project {
    width: 95%;
  }

  #projects ul {
    padding: 0 10px 0 10px;
  }
}

/* ------------------------------- RESUME ------------------------------- */

#resume .container {
  margin: auto;
  width: 75%;
  max-width: 780px;
  height: 100%;
  border-radius: 25px;
  text-align: center;
}

#resume h1 {
  margin-top: -15px;
  font-family: "Roboto Slab", sans-serif;
  font-weight: bold;
  color: white;
}

#resume a.button {
  display: inline-block;
  width: 175px;
  padding: 1.2em 1.2em;
  border: 0.1em solid #FFFFFF;
  margin: 25px auto;
  border-radius: 15px;
  text-decoration: none;
  font-family:'Roboto Slab',sans-serif;
  font-weight: bold;
  color:#FFFFFF;
  text-align:center;
  transition: all 0.2s;
}

#resume a.button:hover {
  color:gray;
  background-color:#FFFFFF;
}

/* ------------------------------- CONTACT ------------------------------- */

#contact .container {
  margin: auto;
  width: 80%;
  height: auto;
  text-align: center;
  padding: 20px;
  display: flex;
  justify-content: center;
  color: black;
}

#contact .icon {
  height: 50px;
  margin: 5px;
}

#contact .box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #EEE;
  width: 80px;
  margin: 5px;
  padding: 5px;
  border-radius: 15px;
  font-size: 10px;
}